body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  color: white;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  padding: 0px calc((100vw - 1200px) / 2);
  background-color: #00d1b2;
  align-items: center;
}
div .logo {
  font-size: 2.2rem;
  margin-left: 10px;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
}
.nav ul {
  display: flex;
  margin-right: 10px;
}
ul li {
  list-style: none;
  padding-left: 1rem;
  font-size: 1.2rem;
}

input {
  width: 50%;
  height: 55px;
  padding: 10px 15px;
  font-size: 1.2rem;
  margin: 50px 0;
  color: #6d6b6b;
  box-sizing: border-box;
  border: 3px solid #f4f4f4;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.search {
  display: flex;
  justify-content: center;
  margin-bottom: 0px;
  height: 120px;
}
button {
  height: 55px;
  border: none;
  font-size: 1.2rem;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  width: 100px;
  background-color: #3273dc;
  color: white;
  margin: 50px 0;
  cursor: pointer;
}
.main {
  padding: 0px calc((100vw - 1300px) / 2);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.beer {
  border: 3px solid #f4f4f4;
  border-radius: 6px;
  display: flex;
  width: 380px;
  height: 220px;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 10px 10px;
}
img {
  max-width: 60px;
  max-height: 100px;
  margin-left: 20px;
  margin-right: 50px;
}
.description {
  flex: 2;
  display: flex;
  flex-direction: column;
  padding: 10px;
  color: rgb(124, 116, 116);
  font-size: 1rem;
  height: 150px;
}
.title {
  color: #525252;
}
p {
  font-family: "Poppins", sans-serif;
  color: #6d6b6b;
  line-height: 1.5rem;
  margin-top: 0px;
}
.icon {
  position: absolute;
  top: 8%;
  right: 8%;
  font-size: 0.8rem;
  color: #5ae0cc;
}

.noBeer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.goBackBtn {
  height: 55px;
  border: none;
  font-size: 1.2rem;
  border-radius: 20px;

  width: 300px;
  background-color: #3273dc;
  color: white;
  margin: 50px 0;
  cursor: pointer;
}

@media Screen and (max-width: 490px) {
  .nav {
    height: 7vh;
  }
  .nav .logo {
    font-size: 1.2rem;
  }
  ul li {
    font-size: 1rem;
  }
  .search {
    height: 100px;
  }
  button {
    height: 30px;
  }
  input {
    height: 30px;
  }
  img {
    margin-left: 5px;
    margin-right: 10px;
  }
  .beer {
    width: 370px;
    height: 170px;
  }
  .goBackBtn {
    height: 30px;
    width: 280px;
  }
  p {
    font-size: 1rem;
  }
  .title {
    margin-bottom: 5px;
  }
}
